// components/forms/TransactionForm.tsx
"use client";
import { useState, useEffect } from 'react';
import { useFinance } from '@/context/FinanceContext';
import { Category, FixedExpenseRule } from '@/types';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Save, AlertCircle } from 'lucide-react';
import { v4 as uuidv4 } from 'uuid';
import { Alert, AlertDescription } from '@/components/ui/alert';

// Props para saber si es modo Ingreso o Gasto directamente
interface Props {
    defaultType?: 'income' | 'expense';
    onClose?: () => void;
}

export default function TransactionForm({ defaultType = 'expense', onClose }: Props) {
  const { addTransaction, addFixedRule, accounts, categories } = useFinance();
  
  const [type, setType] = useState<'income' | 'expense'>(defaultType);
  const [amountReal, setAmountReal] = useState(''); // Lo que realmente pagué
  const [amountBudgeted, setAmountBudgeted] = useState(''); // Lo que esperaba pagar (solo gastos)
  const [desc, setDesc] = useState('');
  const [accountId, setAccountId] = useState('');
  const [categoryId, setCategoryId] = useState('');
  const [subCategory, setSubCategory] = useState('');
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]); // Formato YYYY-MM-DD para input date
  
  // Lógica de Gasto Fijo
  const [isFixedBooking, setIsFixedBooking] = useState(false); // ¿Quiero guardar esto como regla futura?
  const [dayOfMonth, setDayOfMonth] = useState('1'); // Para la regla fija

  const selectedCategory = categories.find(c => c.id === categoryId);
  const canBeFixed = type === 'expense' && selectedCategory?.isFixedExpenseCandidate;

  // Calcular posible ahorro si es gasto
  const calculatedSavings = (type === 'expense' && amountBudgeted && amountReal) 
    ? parseFloat(amountBudgeted) - parseFloat(amountReal) 
    : 0;

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!amountReal || !accountId || !categoryId) return;

    const finalRealAmount = parseFloat(amountReal);
    // Si es ingreso, el presupuestado es igual al real por defecto para simplificar
    const finalBudgetedAmount = type === 'income' ? finalRealAmount : (amountBudgeted ? parseFloat(amountBudgeted) : finalRealAmount);

    // 1. Crear la transacción actual
    addTransaction({
      amount: finalRealAmount,
      budgetedAmount: finalBudgetedAmount,
      type,
      accountId,
      categoryId,
      subCategoryId: subCategory || undefined,
      description: desc,
      date: new Date(date).toISOString(),
      isFixed: isFixedBooking, // Marca esta transacción inicial como "parte de un fijo"
    });

    // 2. Si el usuario quiere que se repita a futuro, crear la REGLA
    if (isFixedBooking && canBeFixed) {
        const newRule: FixedExpenseRule = {
            id: uuidv4(),
            description: desc || selectedCategory.name,
            categoryId,
            subCategoryId: subCategory || undefined,
            budgetedAmount: finalBudgetedAmount, // La regla usa el valor PRESUPUESTADO
            dayOfMonth: parseInt(dayOfMonth),
            accountIdToCharge: accountId,
            isActive: true
        };
        addFixedRule(newRule);
        alert("Regla de gasto fijo creada para los próximos meses.");
    }
    
    // Reset y cerrar
    resetForm();
    if (onClose) onClose();
  };

  const resetForm = () => {
    setAmountReal(''); setAmountBudgeted(''); setDesc(''); setSubCategory(''); setIsFixedBooking(false);
  }

  const filteredCategories = categories.filter(c => {
      if (type === 'income') return c.type === 'income';
      return c.type === 'expense' || c.type === 'payment';
  });

  return (
    <div className="space-y-4 text-white">
      {/* Header visual */}
      <div className={`h-2 w-full rounded-full mb-4 ${type === 'income' ? 'bg-green-500' : 'bg-red-500'}`} />

      <form onSubmit={handleSubmit} className="space-y-4">
        {/* Fecha */}
        <div className="space-y-1">
            <Label>Fecha del movimiento</Label>
            <Input type="date" value={date} onChange={e => setDate(e.target.value)} className="bg-zinc-800 border-zinc-700" />
        </div>

        {/* Montos: Real vs Presupuestado */}
        <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
            <Label className={type === 'income' ? 'text-green-400' : 'text-red-400 font-bold'}>
                {type === 'income' ? 'Valor Recibido (Real)' : 'Valor Pagado (Real)'} *
            </Label>
            <Input 
                type="number" step="0.01" placeholder="$0" required
                className="text-xl font-bold bg-zinc-900 border-zinc-700 h-12"
                value={amountReal} onChange={e => setAmountReal(e.target.value)}
            />
            </div>
            
            {type === 'expense' && (
                <div className="space-y-2">
                <Label className="text-zinc-400">Valor Presupuestado (Esperado)</Label>
                <Input 
                    type="number" step="0.01" placeholder="Opcional"
                    className="text-lg bg-zinc-800 border-zinc-700 h-12 opacity-80"
                    value={amountBudgeted} onChange={e => setAmountBudgeted(e.target.value)}
                />
                <p className="text-xs text-zinc-500">Si se deja vacío, se asume igual al real.</p>
                </div>
            )}
        </div>

        {/* Alerta de Ahorro Automático */}
        {calculatedSavings > 0 && (
            <Alert className="bg-green-900/30 border-green-800 text-green-300">
                <AlertCircle className="h-4 w-4" />
                <AlertDescription>
                    ¡Genial! Te ahorraste <strong>${calculatedSavings.toLocaleString()}</strong> sobre lo presupuestado. 
                    Esto se moverá automáticamente a tu ahorro en Davivienda TH al guardar.
                </AlertDescription>
            </Alert>
        )}

        {/* Cuenta y Categoría */}
        <div className="grid grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label>{type === 'income' ? 'Destino (Entra a)' : 'Origen (Sale de)'} *</Label>
            <Select onValueChange={setAccountId} required>
              <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar Cuenta" /></SelectTrigger>
              <SelectContent>
                {accounts.filter(a => !a.isHidden).map(acc => (
                  <SelectItem key={acc.id} value={acc.id}>{acc.bank} - {acc.name}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label>Categoría *</Label>
            <Select onValueChange={setCategoryId} required>
              <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar Categ." /></SelectTrigger>
              <SelectContent>
                {filteredCategories.map(cat => (
                  <SelectItem key={cat.id} value={cat.id}>{cat.name}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>
        
        {/* Subcategoría y Descripción */}
        <div className="grid grid-cols-2 gap-4">
            {selectedCategory?.subCategories && (
                <div className="space-y-2">
                    <Label>Detalle / Subcategoría</Label>
                    <Select onValueChange={setSubCategory}>
                    <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Opcional" /></SelectTrigger>
                    <SelectContent>
                        {selectedCategory.subCategories.map(sub => (
                        <SelectItem key={sub} value={sub}>{sub}</SelectItem>
                        ))}
                    </SelectContent>
                    </Select>
                </div>
            )}
            <div className="space-y-2 col-span-2">
            <Label>Notas / Descripción</Label>
            <Input className="bg-zinc-800 border-zinc-700" placeholder="Ej: Regalo mamá, Cine..." value={desc} onChange={e => setDesc(e.target.value)} />
            </div>
        </div>


        {/* Sección de Gasto Fijo Recurrente */}
        {canBeFixed && (
           <div className="rounded-lg border border-indigo-800 bg-indigo-950/30 p-4 space-y-3">
             <div className="flex items-center justify-between">
               <Label htmlFor="fixed-mode" className="font-bold text-indigo-300">¿Repetir todos los meses?</Label>
               <Switch id="fixed-mode" checked={isFixedBooking} onCheckedChange={setIsFixedBooking} className="data-[state=checked]:bg-indigo-500" />
             </div>
             
             {isFixedBooking && (
                <div className="flex items-center gap-4 animate-in fade-in slide-in-from-top-2">
                    <Label className="whitespace-nowrap text-sm">Día del mes:</Label>
                     <Select value={dayOfMonth} onValueChange={setDayOfMonth}>
                        <SelectTrigger className="w-[80px] bg-zinc-800 border-indigo-700"><SelectValue placeholder="Día" /></SelectTrigger>
                        <SelectContent className="max-h-[200px]">
                            {Array.from({length: 31}, (_, i) => i + 1).map(day => (
                                <SelectItem key={day} value={day.toString()}>{day}</SelectItem>
                            ))}
                        </SelectContent>
                     </Select>
                     <p className="text-xs text-zinc-400 leading-tight">
                        Se creará automáticamente el día {dayOfMonth} de cada mes usando el valor "Presupuestado" (${amountBudgeted || amountReal}).
                     </p>
                </div>
             )}
           </div>
        )}


        <Button type="submit" className={`w-full h-12 text-lg ${type === 'income' ? 'bg-green-600 hover:bg-green-700' : 'bg-red-600 hover:bg-red-700'}`}>
          <Save className="mr-2 h-5 w-5" /> Registrar {type === 'income' ? 'Ingreso' : 'Gasto'}
        </Button>
      </form>
    </div>
  );
}