"use client";

import { useMemo } from 'react';
import { Transaction } from '@/types';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend } from 'recharts';
import { format, startOfMonth, endOfMonth, eachDayOfInterval, isSameDay } from 'date-fns';
import { es } from 'date-fns/locale';

interface Props {
    transactions: Transaction[];
    period: string;
    currentDate: Date;
}

export default function ComplexFinanceChart({ transactions, period, currentDate }: Props) {
    
    const data = useMemo(() => {
        const start = startOfMonth(currentDate);
        const end = endOfMonth(currentDate);
        const days = eachDayOfInterval({ start, end });

        let cumulativeBalance = 0;

        return days.map(day => {
            const dailyTx = transactions.filter(t => isSameDay(new Date(t.date), day));
            
            // 1. Ingresos
            const income = dailyTx.filter(t => t.type === 'income' && t.categoryId !== 'cat_sys_savings').reduce((acc, t) => acc + t.amount, 0);
            
            // 2. Gastos (Excluyendo movimientos internos de ahorro para no duplicar)
            const expense = dailyTx.filter(t => t.type === 'expense').reduce((acc, t) => acc + t.amount, 0);
            
            // 3. Ahorros (Categorías específicas de ahorro)
            const savings = dailyTx.filter(t => t.type === 'income' && (t.categoryId === 'cat_sys_savings' || t.categoryId === 'cat_ahorro_meta'))
                                   .reduce((acc, t) => acc + t.amount, 0);

            // 4. Restante (Cash Flow Neto del día) = Ingreso - Gasto - Ahorro
            // Si quieres ver el acumulado del mes, sumamos al anterior.
            const netFlow = income - expense - savings;
            cumulativeBalance += netFlow;

            return {
                day: format(day, 'd'),
                fullDate: format(day, 'dd MMM', { locale: es }),
                ingreso: income,
                gasto: expense,
                ahorro: savings,
                restante: cumulativeBalance // Mostramos el acumulado (cuánto me queda en el bolsillo a fin de mes)
            };
        });
    }, [transactions, currentDate]);

    return (
        <div className="w-full h-[350px]">
            <ResponsiveContainer width="100%" height="100%">
                <AreaChart data={data} margin={{ top: 10, right: 10, left: -20, bottom: 0 }}>
                    <defs>
                        <linearGradient id="colorIngreso" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#10b981" stopOpacity={0.2}/>
                            <stop offset="95%" stopColor="#10b981" stopOpacity={0}/>
                        </linearGradient>
                        <linearGradient id="colorGasto" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#f43f5e" stopOpacity={0.2}/>
                            <stop offset="95%" stopColor="#f43f5e" stopOpacity={0}/>
                        </linearGradient>
                        <linearGradient id="colorAhorro" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#3b82f6" stopOpacity={0.3}/>
                            <stop offset="95%" stopColor="#3b82f6" stopOpacity={0}/>
                        </linearGradient>
                        <linearGradient id="colorRestante" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#a855f7" stopOpacity={0.1}/>
                            <stop offset="95%" stopColor="#a855f7" stopOpacity={0}/>
                        </linearGradient>
                    </defs>
                    <CartesianGrid strokeDasharray="3 3" stroke="#27272a" vertical={false} />
                    <XAxis dataKey="day" stroke="#52525b" tickLine={false} axisLine={false} interval={2} />
                    <YAxis stroke="#52525b" tickLine={false} axisLine={false} tickFormatter={(value) => `$${value/1000}k`} />
                    <Tooltip 
                        contentStyle={{ backgroundColor: '#09090b', borderColor: '#27272a', borderRadius: '8px' }}
                        itemStyle={{ padding: 0 }}
                        formatter={(value: number, name: string) => [`$${value.toLocaleString()}`, name.toUpperCase()]}
                        labelFormatter={(label) => `Día ${label}`}
                    />
                    <Legend wrapperStyle={{ paddingTop: '10px' }} />
                    
                    {/* Áreas superpuestas */}
                    <Area type="monotone" dataKey="ingreso" name="Ingresos" stroke="#10b981" fill="url(#colorIngreso)" strokeWidth={2} />
                    <Area type="monotone" dataKey="gasto" name="Gastos" stroke="#f43f5e" fill="url(#colorGasto)" strokeWidth={2} />
                    <Area type="monotone" dataKey="ahorro" name="Ahorro" stroke="#3b82f6" fill="url(#colorAhorro)" strokeWidth={2} />
                    {/* Línea de Restante (Acumulado) más gruesa para destacar */}
                    <Area type="monotone" dataKey="restante" name="Restante Acumulado" stroke="#a855f7" fill="url(#colorRestante)" strokeWidth={3} strokeDasharray="5 5"/>
                </AreaChart>
            </ResponsiveContainer>
        </div>
    );
}