"use client";
import { useState, useMemo, useEffect } from 'react';
import { useFinance } from '@/context/FinanceContext';
import TransactionForm from '@/components/forms/TransactionForm';
import ComplexFinanceChart from '@/components/dashboard/ComplexFinanceChart';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Dialog, DialogContent, DialogTrigger, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { PlusCircle, MinusCircle, CalendarIcon, Download, BellRing, CheckCircle2 } from 'lucide-react';
import { isSameMonth, isSameYear, isSameDay, format, getDate } from 'date-fns';
import { es } from 'date-fns/locale';
import Link from 'next/link';
import { Settings } from 'lucide-react';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import { cn } from '@/lib/utils';

export default function Dashboard() {
  const { transactions, categories, accounts, fixedRules, markRuleAsPaid } = useFinance();
  const [period, setPeriod] = useState('day');
  
  // ESTADO DE FECHA: Siempre inicia en HOY
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date());
  
  // RESETEAR FECHA AL CAMBIAR DE PESTAÑA
  useEffect(() => {
      setSelectedDate(new Date());
  }, [period]);

  // --- LOGICA DE GASTOS PENDIENTES (RECORDATORIOS) ---
  const todayDay = getDate(new Date());
  const pendingRules = fixedRules.filter(rule => {
      // Es hoy y NO se ha marcado como pagado este mes (usando una lógica simple de string YYYY-MM)
      const currentMonthStr = format(new Date(), 'yyyy-MM');
      const isDueToday = rule.dayOfMonth === todayDay;
      const alreadyPaid = rule.lastPaidDate?.startsWith(currentMonthStr);
      return isDueToday && !alreadyPaid;
  });

  const sendWhatsAppReminder = (ruleName: string, amount: number) => {
      const msg = `Hola! Recuerda realizar el pago de: ${ruleName} por $${amount.toLocaleString()} hoy.`;
      window.open(`https://wa.me/?text=${encodeURIComponent(msg)}`, '_blank');
  };

  // --- FILTRO DE DATOS (Cards y Tablas obedecen a selectedDate) ---
  const filteredTx = useMemo(() => {
    if (!selectedDate) return [];
    return transactions.filter(t => {
        const tDate = new Date(t.date);
        if (period === 'day') return isSameDay(tDate, selectedDate);
        if (period === 'month') return isSameMonth(tDate, selectedDate);
        if (period === 'year') return isSameYear(tDate, selectedDate);
        return true;
    }).sort((a,b) => new Date(b.date).getTime() - new Date(a.date).getTime());
  }, [transactions, period, selectedDate]);

  // Calcular KPIs (igual que antes)
  const incomeReal = filteredTx.filter(t => t.type === 'income').reduce((acc, curr) => acc + curr.amount, 0);
  const expenseReal = filteredTx.filter(t => t.type === 'expense').reduce((acc, curr) => acc + curr.amount, 0);
  const savingsReal = filteredTx.filter(t => t.type === 'income' && t.categoryId === 'cat_sys_savings').reduce((acc, curr) => acc + curr.amount, 0);
  const remaining = incomeReal - expenseReal;

  // --- EXPORTAR A EXCEL ---
  const handleExportExcel = () => {
      const ws = XLSX.utils.json_to_sheet(transactions.map(t => ({
          Fecha: format(new Date(t.date), 'dd/MM/yyyy HH:mm'),
          Tipo: t.type === 'income' ? 'Ingreso' : 'Gasto',
          Monto: t.amount,
          Categoria: categories.find(c => c.id === t.categoryId)?.name || 'N/A',
          SubCategoria: t.subCategoryId || '',
          Cuenta: accounts.find(a => a.id === t.accountId)?.name || 'N/A',
          Descripcion: t.description
      })));
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "Movimientos");
      const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
      const data = new Blob([excelBuffer], {type: "application/octet-stream"});
      saveAs(data, `Reporte_Financiero_${format(new Date(), 'yyyy-MM-dd')}.xlsx`);
  };

  return (
    <div className="space-y-6 pb-24">
      {/* Header */}
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
        <div className="flex items-center gap-4">
            <div>
                <h1 className="text-3xl font-extrabold bg-clip-text text-transparent bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400">
                HOME FINANCE
                </h1>
                <p className="text-sm text-zinc-400 capitalize">
                    {selectedDate ? format(selectedDate, "EEEE, d 'de' MMMM", { locale: es }) : 'Selecciona fecha'}
                </p>
            </div>
            <Link href="/admin">
                <Button variant="outline" size="icon" className="border-zinc-700 bg-zinc-900"><Settings className="h-5 w-5" /></Button>
            </Link>
        </div>
        
        <div className="flex gap-2 w-full md:w-auto flex-wrap">
            <Button variant="secondary" onClick={handleExportExcel} className="flex-1 md:flex-none">
                <Download className="mr-2 h-4 w-4" /> Exportar
            </Button>
            
            <Dialog>
                <DialogTrigger asChild><Button className="flex-1 bg-emerald-600"><PlusCircle className="mr-2 h-4 w-4" /> Ingreso</Button></DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800"><DialogTitle>Nuevo Ingreso</DialogTitle><TransactionForm defaultType="income" /></DialogContent>
            </Dialog>

            <Dialog>
                <DialogTrigger asChild><Button className="flex-1 bg-rose-600"><MinusCircle className="mr-2 h-4 w-4" /> Gasto</Button></DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800"><DialogTitle>Nuevo Gasto</DialogTitle><TransactionForm defaultType="expense" /></DialogContent>
            </Dialog>
        </div>
      </div>

      {/* SECCIÓN DE ALERTAS DE PAGO (RECORDATORIOS) */}
      {pendingRules.length > 0 && (
          <div className="grid gap-3">
              {pendingRules.map(rule => (
                  <div key={rule.id} className="flex items-center justify-between p-4 bg-indigo-950/30 border border-indigo-500/30 rounded-xl animate-in slide-in-from-top-2">
                      <div className="flex items-center gap-3">
                          <div className="p-2 bg-indigo-500/20 rounded-full text-indigo-300"><BellRing className="h-5 w-5 animate-pulse" /></div>
                          <div>
                              <p className="font-bold text-indigo-100">¡Pago Recurrente Hoy!</p>
                              <p className="text-sm text-indigo-300">{rule.description} - ${rule.budgetedAmount.toLocaleString()}</p>
                          </div>
                      </div>
                      <div className="flex gap-2">
                          <Button size="sm" variant="outline" className="border-green-600 text-green-400 hover:bg-green-900/20" 
                                  onClick={() => sendWhatsAppReminder(rule.description, rule.budgetedAmount)}>
                              WhatsApp
                          </Button>
                          <Dialog>
                             <DialogTrigger asChild>
                                 <Button size="sm" className="bg-indigo-600">Pagar</Button>
                             </DialogTrigger>
                             <DialogContent className="bg-zinc-950 border-zinc-800">
                                 <DialogTitle>Confirmar Pago Recurrente</DialogTitle>
                                 {/* Aquí pasamos datos prellenados al form (esto requiere actualizar TransactionForm para aceptar props iniciales, lo cual asumimos hecho o el usuario lo hace manual) */}
                                 <TransactionForm defaultType="expense" onClose={() => markRuleAsPaid(rule.id, format(new Date(), 'yyyy-MM'))} /> 
                             </DialogContent>
                          </Dialog>
                      </div>
                  </div>
              ))}
          </div>
      )}

      {/* GRÁFICA (Siempre muestra el mes actual para ver tendencias, independientemente del filtro diario) */}
      <Card className="bg-zinc-900/50 border-zinc-800 shadow-xl">
        <CardHeader className="pb-2"><CardTitle>Tendencia Mensual</CardTitle></CardHeader>
        <CardContent>
           <ComplexFinanceChart transactions={transactions} period="month" currentDate={new Date()} />
        </CardContent>
      </Card>

      {/* TABS CON FILTRO DE FECHA INDEPENDIENTE */}
      <Tabs value={period} onValueChange={(v) => setPeriod(v)} className="space-y-6">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 bg-zinc-900/50 p-2 rounded-lg border border-zinc-800">
            <TabsList className="bg-zinc-950 border border-zinc-800">
                <TabsTrigger value="day">Día</TabsTrigger>
                <TabsTrigger value="month">Mes</TabsTrigger>
                <TabsTrigger value="year">Año</TabsTrigger>
            </TabsList>
            
            {/* SELECTOR DE FECHA (DATE PICKER) */}
            <Popover>
                <PopoverTrigger asChild>
                    <Button variant={"outline"} className={cn("w-[240px] justify-start text-left font-normal bg-zinc-950 border-zinc-700", !selectedDate && "text-muted-foreground")}>
                        <CalendarIcon className="mr-2 h-4 w-4" />
                        {selectedDate ? format(selectedDate, "PPP", { locale: es }) : <span>Seleccionar fecha</span>}
                    </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0 bg-zinc-900 border-zinc-800" align="end">
                    <Calendar
                        mode="single"
                        selected={selectedDate}
                        onSelect={setSelectedDate}
                        initialFocus
                        className="p-3 pointer-events-auto" 
                    />
                </PopoverContent>
            </Popover>
        </div>

        <TabsContent value={period} className="space-y-6">
          {/* Tarjetas KPI y Tabla (Código existente...) */}
           <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
              <KPICard title="Ingresos" value={incomeReal} color="text-emerald-400" />
              <KPICard title="Gastos" value={expenseReal} color="text-rose-400" />
              <KPICard title="Restante" value={remaining} color="text-indigo-400" />
              <KPICard title="Ahorro" value={savingsReal} color="text-blue-400" />
           </div>

           {/* Tabla de Movimientos Filtrada */}
           <Card className="bg-zinc-900 border-zinc-800">
               <CardHeader><CardTitle>Detalle ({filteredTx.length})</CardTitle></CardHeader>
               <CardContent>
                   <div className="space-y-2">
                       {filteredTx.map(tx => (
                           <div key={tx.id} className="flex justify-between p-3 bg-zinc-950 rounded border border-zinc-800/50">
                               <div>
                                   <p className="font-bold">{tx.description}</p>
                                   <p className="text-xs text-zinc-500">{format(new Date(tx.date), 'HH:mm')} • {accounts.find(a => a.id === tx.accountId)?.name}</p>
                               </div>
                               <span className={tx.type === 'income' ? 'text-emerald-500' : 'text-rose-500'}>
                                   ${tx.amount.toLocaleString()}
                               </span>
                           </div>
                       ))}
                       {filteredTx.length === 0 && <p className="text-center text-zinc-500 py-4">Sin movimientos para esta fecha.</p>}
                   </div>
               </CardContent>
           </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}

function KPICard({ title, value, color }: any) {
    return (
        <Card className="bg-zinc-900 border-zinc-800"><CardContent className="p-4">
            <p className="text-xs text-zinc-500 uppercase">{title}</p>
            <p className={`text-2xl font-bold ${color}`}>${value.toLocaleString()}</p>
        </CardContent></Card>
    )
}